@use "global" as *;


.archive-residence{
    width: 100%;
    padding-left: rem(20);
    padding-right: rem(20);
    display: flex;
    &__wrapper{
        display: flex;
        flex-direction: column;
        .new-residence__card{
            width: 100%;
        }

    }
}

@include mq (){
    .archive-residence{
        margin: 0 auto;
        padding-left: rem(50);
        padding-right: rem(50);

        &__wrapper{
            display: flex;
            flex-direction: row;
            .new-residence__card{
                width: 80%;
            }
            .residence-sidebar{
                width: 20%;
            }
        }

    }
}

// サイドバー

.residence-sidebar{
    margin-left: rem(30);
    .sidebar-residence {
        border-radius: rem(10);
        &__title{
            padding-top:rem(10) ;
            padding-bottom:rem(10) ;
            padding-left: rem(5);
            background-color: rgba(235, 97, 0, .13);
            margin-bottom: rem(8);
            font-size: rem(18);
            font-weight: 700;
        }
       
    }
    .sidebar-category{
        &__unit{
            margin-bottom: rem(15);
        }
       
        &__list{
            display: flex;
            flex-direction: column;
            gap: rem(10);
            padding-top: rem(5);
            padding-bottom: rem(5);
            border-bottom: 1px solid #333;
            margin-bottom: rem(5);
        }
        &__list--orange{
           
            background-color: #f5f5f5;
        }
        &__list--green{
          
            background-color: #f5f5f5;
        }
        
    }
    .sidebar-tag__unit{
        margin-bottom: rem(15);
        a{
            display: inline-block;
            padding: rem(5) rem(10);
            background-color: #f5f5f5;
            border-radius: rem(10);
            font-size: rem(14);
            margin-bottom: rem(5);
        }
    }
}




